/*BOXED LAYOUT*/
/*GRID - media queries breakpoints*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  line-height: 1.5;
}

html,
body {
  height: 100%;
  font-family: "Montserrat", sans-serif;
  scroll-behavior: smooth;
}
html .has-scroll,
body .has-scroll {
  overflow: hidden;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: var(--color-black);
}

.pd-70 {
  padding: 70px 0;
}

h2 {
  font-weight: 500;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.container-fluid {
  width: 100%;
  padding-right: 30px;
  padding-left: 30px;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 767px) {
  .container-fluid {
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1024px;
  }
}
:root {
  --color-primary: #009DAE;
  --color-secondary: #C2FFF9;
  --color-white: #fff;
  --color-black: #000;
  --bg-grey: #fafafa;
}

.btn-main {
  color: var(--color-white);
  margin: 0 auto;
  border: 1px solid transparent;
  padding: 18px 30px;
  border-radius: 8px;
  cursor: pointer;
  width: max-content;
  margin-top: 16px;
  background: linear-gradient(to left, var(--color-primary) 50%, transparent 50%) right;
  background-size: 200%;
  transition: all 0.4s ease-out;
}
.btn-main:hover {
  background-position: left;
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
}

.resume__btn {
  color: var(--color-primary);
  border: 1px solid var(--color-primary);
  padding: 18px 30px;
  border-radius: 8px;
  cursor: pointer;
  background: linear-gradient(to left, transparent 50%, var(--color-primary) 50%) right;
  background-size: 200%;
  transition: 0.4s ease-out;
}
.resume__btn:hover {
  background-position: left;
  color: var(--color-white);
}

.btn-project {
  margin-top: 30px;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-weight: 500;
  position: relative;
}
.btn-project:after {
  content: "";
  position: absolute;
  height: 2px;
  width: 25%;
  display: block;
  background-color: var(--color-primary);
  bottom: 0;
  left: 0;
}
.btn-project img {
  width: 20px;
  height: 20px;
  margin-left: 8px;
  transition: all 0.2s ease;
}
.btn-project:hover img {
  transform: rotate(45deg);
}

.header {
  height: 100vh;
  background: url("https://i.gifer.com/IrM.gif") no-repeat center center fixed;
  background-size: cover;
  position: relative;
}
.header.active {
  background-color: var(--color-black);
}
.header__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: var(--color-white);
}
.header__text a {
  display: block;
}
.header__text .btn-main {
  margin-top: 2.5rem;
}
.header__name {
  font-size: 3.125rem;
}
.header__name span {
  color: var(--color-primary);
}
.header__desc {
  font-size: 2.1875rem;
}
@media (max-width: 1200px) {
  .header {
    height: 60vh;
  }
  .header__name {
    font-size: 45px;
  }
  .header__desc {
    font-size: 30px;
  }
}
@media (max-width: 768px) {
  .header__name {
    min-width: max-content;
  }
  .header__desc {
    font-size: 30px;
  }
}
@media (max-width: 576px) {
  .header__name {
    font-size: 30px;
  }
  .header__desc {
    font-size: 20px;
  }
  .header .btn-main {
    padding: 10px 20px;
  }
}

.menu {
  position: fixed;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.712);
  z-index: 10;
  height: 80px;
}
.menu .container-fluid {
  height: 100%;
}
.menu .logo img {
  width: 30px;
  height: 30px;
}
.menu__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.menu a {
  color: white;
}
.menu-list ul {
  display: flex;
}
.menu-item {
  padding: 1rem 1.25rem;
  margin-left: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  font-weight: bold;
  background: linear-gradient(to left, transparent 50%, var(--color-primary) 50%) right;
  background-size: 350%;
  transition: 0.6s ease-out;
}
.menu-item:hover {
  background-position: left;
}
.menu .nav__icon {
  cursor: pointer;
  display: none;
}
.menu .nav__icon img {
  width: 30px;
  height: 30px;
}

@media (max-width: 768px) {
  .menu-list {
    display: none;
  }
  .menu .nav__icon {
    display: block;
  }
}
.about {
  color: var(--color-black);
  background-color: var(--bg-grey);
}
.about__heading {
  text-align: center;
  margin-bottom: 2.5rem;
}
.about__title {
  font-size: 30px;
}
.about__content {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.about__content h2 {
  padding: 16px 0;
}
.about__content h3 {
  padding: 12px 0;
  color: var(--color-primary);
}
.about__content p {
  margin-bottom: 40px;
  padding-bottom: 10px;
  position: relative;
}
.about__content p:after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 1px;
  background-color: black;
  bottom: 0;
  left: 0;
}
.about__icon {
  margin-right: 5px;
}
.about__intro {
  display: flex;
  flex-direction: column;
  padding-right: 2.5rem;
}
.about__intro img {
  width: 20px;
  height: 20px;
  object-fit: cover;
}
.about__image img {
  border-radius: 10px;
  width: 90%;
  height: 90%;
  object-fit: cover;
}
.about__btn {
  margin-top: 3.75rem;
  text-align: center;
}
.about__btn .btn-main {
  margin-right: 1.25rem;
}

@media (max-width: 768px) {
  .about__content {
    display: block;
  }
  .about__intro {
    padding-right: 0;
  }
  .about__image {
    width: 100%;
    height: 500px;
    margin: 0 auto;
    object-fit: cover;
  }
  .about__image img {
    height: 100%;
    display: block;
    margin: 0 auto;
  }
  .about__btn {
    text-align: center;
  }
}
.skill {
  padding-bottom: 40px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.774), rgba(0, 0, 0, 0.5)), url("../images/lap.jpg") no-repeat center fixed;
  background-size: cover;
  color: var(--color-white);
}
.skill__heading {
  text-align: center;
}
.skill__content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  text-align: center;
  margin-top: 40px;
  gap: 80px;
}
.skill__item {
  background-color: transparent;
  padding: 50px;
  border-radius: 15px;
  box-shadow: 0px 5px 12px #eee;
}
.skill__item img {
  width: 50px;
  height: 50px;
  object-fit: cover;
}
.skill p {
  margin: 8px 0;
}
.skill .resume__btn {
  max-width: max-content;
  margin: 0 auto;
  margin-top: 60px;
}

@media (max-width: 768px) {
  .skill__content {
    gap: 3.125rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 576px) {
  .skill__content {
    grid-template-columns: auto;
  }
}
.experience__heading {
  text-align: center;
  margin-bottom: 50px;
}
.experience__content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  gap: 100px;
  margin-bottom: 50px;
  position: relative;
}
.experience__content::before {
  content: "";
  background-color: var(--color-primary);
  width: 100%;
  height: 2px;
  display: block;
  position: absolute;
  top: 5px;
  left: 0;
  transform: translateY(-2px);
}
.experience__item .dot {
  width: 10px;
  height: 10px;
  background-color: var(--color-primary);
  border-radius: 50%;
  position: relative;
  margin: 0 auto;
  display: block;
}
.experience__item .dot::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background-color: inherit;
  animation: fade 1s forwards infinite linear;
}
@keyframes fade {
  to {
    transform: scale(2);
    opacity: 0;
  }
}
.experience__item .status {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 20px;
  background-color: var(--color-primary);
  padding: 50px 20px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0px 5px 15px var(--color-primary);
  height: 100%;
}
.experience__item .status h3,
.experience__item .status h4,
.experience__item .status h5 {
  font-weight: 500;
  padding: 5px 0;
  color: var(--color-white);
}
.experience__item .status p {
  padding: 5px;
  opacity: 0.8;
}

@media (max-width: 768px) {
  .experience__content {
    gap: 2.1875rem;
  }
}
@media (max-width: 576px) {
  .experience__content {
    grid-template-columns: auto;
  }
  .experience__content:before {
    display: none;
  }
  .experience__item .dot {
    display: none;
  }
}
.project__heading {
  text-align: center;
}
.project__item {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  margin-top: 50px;
  background-color: var(--bg-grey);
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0px 5px 12px #eee;
  margin-bottom: 50px;
}
.project__item--reverse {
  flex-direction: row-reverse;
}
.project__item--reverse .project__infor {
  margin: 0;
  margin-right: 100px;
}
.project__item--reverse .text a {
  color: var(--color-primary);
}
.project__image {
  overflow: hidden;
  border-radius: 10px;
  cursor: pointer;
  position: relative;
}
.project__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s ease;
}
.project__image:hover img {
  transform: scale(1.2);
}
.project__image .overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.5 ease-in;
  background-color: rgba(0, 0, 0, 0.7);
}
.project__image .text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 22px;
}
.project__image .text a {
  color: var(--color-primary);
}
.project__image .text img {
  width: 20px;
  height: 20px;
  margin-right: 8px;
}
.project__image .text::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: var(--color-primary);
  display: block;
  bottom: 0;
  left: 0;
}
.project__image .resume__btn:hover {
  color: var(--color-white);
}
.project__image:hover .overlay {
  opacity: 1;
}
.project__infor {
  margin-left: 100px;
}
.project__infor h3 {
  margin: 8px 0;
}
.project__infor h5 {
  color: var(--color-primary);
  margin: 8px 0;
}
@media (max-width: 768px) {
  .project__item {
    flex-direction: column;
  }
  .project__infor {
    margin: 0;
  }
  .project__infor h3 {
    margin-top: 1.875rem;
  }
}
@media (max-width: 576px) {
  .project .text {
    font-size: 13px;
  }
  .project .text img {
    width: 13px;
    height: 13px;
  }
}

.contact {
  background-color: var(--color-secondary);
  clip-path: polygon(50% 10%, 100% 0%, 100% 100%, 0 100%, 0 0);
}
.contact .btn-main {
  margin: 0;
}
.contact .btn-main a {
  color: var(--color-white);
}
.contact .btn-main:hover a {
  color: var(--color-primary);
}
.contact__heading {
  text-align: center;
}
.contact__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
  padding: 50px 40px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 5px 12px #eee;
}
.contact__title h3 {
  color: var(--color-primary);
}
.contact__mail, .contact__phone {
  display: flex;
  align-items: center;
  margin: 8px 0;
  max-width: max-content;
}
.contact__mail img, .contact__phone img {
  width: 20px;
  height: 20px;
  margin-right: 8px;
}

@media (max-width: 576px) {
  .contact__content {
    flex-direction: column;
  }
  .contact__title {
    text-align: center;
    margin-bottom: 30px;
  }
  .contact__mail, .contact__phone {
    margin: 8px auto;
  }
}
.footer {
  padding: 20px 0;
  background-color: var(--color-secondary);
}
.footer__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer img {
  width: 30px;
  height: 30px;
  object-fit: cover;
}
.footer .social-media {
  left: 50%;
  transform: translateX(-50%);
}
.footer .social-media__item {
  padding: 8px;
}

@media (max-width: 576px) {
  .footer__content {
    flex-direction: column;
    gap: 20px;
  }
  .footer .social-media {
    left: 0;
    transform: translateX(0);
  }
}
.nav {
  height: 100%;
  width: 100%;
  position: fixed;
  visibility: hidden;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: var(--color-black);
  z-index: 10;
  top: -150%;
  transform: translateY(0);
  transition: all 0.6s ease;
}
.nav ul {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.nav ul > a {
  color: var(--color-white);
}
.nav .close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
.nav .close img {
  width: 50px;
  height: 50px;
}
.nav.active {
  visibility: visible;
  top: 0;
}
.nav .menu-item {
  background: none;
}